.adsBlock {
	&.dark {
		background: $darkGray; }
	&.light {
		background: $adsGray; }
	padding: 10px 0 15px;
	clear: both;

	.placeholder {
		max-width: 728px;
		margin: 0 auto;
		width: 80%;

		img {
			width: 100%;
			height: auto;
			display: block; } } }


.ads-wide {
	background: $adsGray;
	height: 110px;
	padding: 10px 0;
	display: block;
	@include borderbox;
	@include formobile {
		padding: 10px 0;
		width: 100%;
		height: 70px; } }

.ads-box {
	width: 728px;
	height: 90px;
	margin: 0 auto;

	@include formobile {
		width: 100%;
		max-width: 320px;
		height: 50px; }

	&:after {
		content: 'LEADERBOARD';
		display: block;
		width: 100%;
		height: 90px;
		line-height: 90px;
		text-align: center;
		color: $darkGray;
		border: 1px $darkGray solid;
		@include formobile {
			height: 50px;
			line-height: 50px; } } }
